html,
body {
  margin: 30px 0;
  font-family: 'Helvetica Neue', 'Helvetica Neue', Arial, sans-serif;
}

hr {
  border-color: $base_color;
  border-width: 3px;
  max-width: 250px;

  &.light {
    border-color: white;
  }
}

a {
  transition: all 0.35s;
  color: $base_color;
}

a:hover,
a:focus {
  color: #eb3812;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Open Sans', 'Helvetica Neue', Arial, sans-serif;
}

p {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 20px;
}